import { Component, OnInit } from '@angular/core';
import {FormGroup,FormControl,FormBuilder, Validator, Validators} from '@angular/forms';

@Component({
  selector: 'app-sign-up',
  templateUrl: './sign-up.component.html',
  styleUrls: ['./sign-up.component.css']
})
export class SignUpComponent implements OnInit {

  formSignUp : FormGroup;

  constructor(private fb : FormBuilder) {
      // this.formSignUp = new FormGroup({
      //     email : new FormControl('123@gmail.com'),
      //     password : new FormControl(),
          
      //     subjects : new FormGroup({
      //       nodeJS : new FormControl(false),
      //       angular : new FormControl(true),
      //       reactJS : new FormControl(false)
      //     }),

      // });

  }

  ngOnInit(): void {
    this.formSignUp = this.fb.group({
        email : ['', [gmailValidator , Validators.email]],
        password : ['', Validators.minLength(6)],
        
        subjects : this.fb.group({
          nodeJS : false,
          angular : true,
          reactJS : false
        }),

    });
  }

  onSubmit(){
      console.log(this.formSignUp.value);
  }


}

function gmailValidator(formControl : FormControl){
    if(formControl.value.includes('@gmail.com')){
      return null;
    }
    return {gmail : true};
}